
import { defineComponent, watch, ref } from 'vue';

export default defineComponent({
  props: {
    arithmetic: String,
    annotations: String,
    modelValue: String
  },
  emits: ['run', 'update:modelValue'],
  setup(props, { emit }) {
    const myStr = ref(props.modelValue);
    watch(() => myStr.value, () => {
      emit('update:modelValue', myStr.value);
    });
    function run() {
      emit('run');
    }
    return {
      run, myStr
    };
  }
});
